<template>
  <AuthForm
      :info="info"
      v-model:username="formData.username"
      v-model:password="formData.password"
      @finish="registration"
  />
</template>

<script>
import axios from "axios"
import AuthForm from "../components/ui/AuthForm";

export default {
  name: "Registration",
  components: {AuthForm},
  data() {
    return {
      info: {
        username: 'Логин',
        password: 'Пароль',
        btn: 'Регистрация',
      },
      formData: {
        username: "",
        password: "",
      },
    }
  },
  methods: {
    registration() {
      axios({
        method: 'post',
        url: 'http://192.168.1.222:3000/auth',
        headers: {
          Accept: "application/json",
        },
        data: this.formData,
      })
          .then((response) =>{
            console.log(response);
          })
          .catch((error) =>{
            console.log(error);
          });
    }
  },
}
</script>

<style lang="scss" scoped>
.registration {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;

  &__input-wrap {
    margin-bottom: 20px;
    border: 1px solid;
  }
}
</style>
